.navbar-container {
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
  align-items: center;
  gap: 2%;
  width: 100vw;
  height: var(--nav-height);
  position: fixed;
  top: 0;
  transition: all 0.2s ease-in-out;
  z-index: 9991;
}

.navbar-container.scrolling {
  background: #3b3b3bb9;
  transition: all 0.2s ease-in-out;
}

.navbar-container a {
  text-decoration: none;
  color: white;
  font-size: 16px;
  margin: 0 10px;
}

.navbar-container a {
  position: relative;
  text-decoration: none;
}

.navbar-container a::after {
  content: "";
  position: absolute;
  bottom: -2px;
  left: 0;
  width: 0%;
  height: 2px;
  background-color: #ffffff;
  transition: width 0.2s ease;
}

.navbar-container a:hover::after {
  width: 100%;
}

.navbar-container a.selected::after {
  width: 100%;
}
.nav-logo {
  position: fixed;
  top: 0;
  left: 10px;
  z-index: 9999;
  display: flex;
  flex-direction: row;
  gap: 10px;
  justify-content: center;
  align-items: center;
}
.svg-code-logo {
  cursor: pointer;
}

.navbar-dropdown {
  position: relative;
}

.navbar-dropdown-content-container {
  border-bottom-left-radius: 5px;
  border-bottom-right-radius: 5px;
  position: absolute;
  background-color: #9c9c9c26;
  backdrop-filter: blur(5.5px);
  box-shadow: 3px 5px 10px rgb(72 7 97 / 72%);
  -webkit-backdrop-filter: blur(5.5px);
  top: calc(100% + 5px);
  left: 50%;
  transform: translate(-50%, 0);
}

.options-list div {
  padding: 10px;
}
.options-list div:last-child {
  border-bottom-left-radius: 5px;
  border-bottom-right-radius: 5px;
}
.options-list div a {
  margin: 0;
}
.options-list div:hover {
  background: rgba(112, 112, 112, 0.653);
  transition: background 0.2s ease-in;
}

.chakra-portal {
  width: 80%; /* Puoi regolare questa larghezza come preferisci */
  z-index: 99999999999;
  position: relative;
}
