.tooltip {
  position: fixed;
  background-color: #ffffff;
  color: #220040;
  padding: 5px;
  border-radius: 5px;
  box-shadow: 0 2px 5px rgba(255, 255, 255, 0.1);
  z-index: 999;
  transition: all 1s ease-in-out;
}

sup {
  cursor: pointer;
}
.projects-images {
  grid-area: 1 / 1 / 2 / 2;
}
.project-image {
  width: 45vw;
  margin: 10px;
  border-radius: 10px;
}
.projects-container {
  grid-area: 1 / 2 / 2 / 3;
  margin-top: 1%;
}
/* .projects-container > h2 {
    text-align: center;
  } */
.projects-container > h3 {
  text-align: center;
  margin-top: 4%;
}
.projects {
  display: grid;
  justify-content: center;
  align-items: center;
  grid-template-columns: repeat(1, 1fr);
  grid-template-rows: 1fr;
  grid-column-gap: 15px;
  grid-row-gap: 0px;
  padding: 2%;
}
.demolink {
  text-decoration: none;
  color: rgba(176, 111, 255, 0.721);
}
.jumper-svg {
  animation: svgjump 1s infinite;
  -webkit-animation: svgjump 1s infinite;
}
.navigator-element{
  padding: 1%;
}
.navigator-element h3 {
  text-align: center;
}
@keyframes svgjump {
  0% {
    transform: translateY(
      0
    ); /* Inizio dell'animazione: nessuna trasformazione */
  }
  50% {
    transform: translateY(
      -10px
    ); /* Metà dell'animazione: spostamento verso l'alto */
  }
  100% {
    transform: translateY(
      0
    ); /* Fine dell'animazione: torna alla posizione originale */
  }
}
