@import url(Fonts/LexendDeca/LexendDeca.css);
:root {
  --nav-height: 50px;
}
.page-container {
  margin-top: var(--nav-height);
}

.page-container-projects {
  margin-top: calc(var(--nav-height) + 40px);
}
body {
  background: #262626;
  color: rgb(210, 210, 210);
  margin: 0;
  font-family: "Lexend Deca", -apple-system, BlinkMacSystemFont, "Segoe UI",
    "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans",
    "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
code {
  font-family: "workbench", source-code-pro, Menlo, Monaco, Consolas,
    "Courier New", monospace;
  font-weight: 100;
}
.keyword {
  color: #b560ff;
  font-weight: bold;
}
