@import url(../../Fonts/Workbench/Workbench.css);

.hero-container {
  display: grid;
  justify-content: center;
  align-items: center;
  /* grid-template-columns: repeat(2, 1fr); */
  grid-template-rows: 1fr;
  grid-column-gap: 10px;
  grid-row-gap: 0px;
  padding: 2%;
}
.workbench-style {
  font-family: "workbench", sans-serif;
  font-weight: 100 !important;
}
.welcome-container-left {
  grid-area: 1 / 1 / 2 / 2;
}
.welcome-container-right {
  grid-area: 1 / 2 / 2 / 3;
  justify-self: center;
}
.go-to-projects {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center; /* Centra l'elemento orizzontalmente */
  position: relative;
  left: 45%;
  border-radius: 50%; /* Per rendere il bottone rotondo */
  background: #2f0e58;
  width: 100px; /* Larghezza del bottone */
  height: 100px; /* Altezza del bottone */
  cursor: pointer; /* Cambia il cursore al passaggio del mouse */
  border: none; /* Rimuove il bordo del bottone */
  outline: none; /* Rimuove l'outline del bottone */
  transition: all 0.2s ease-in-out;
}

.go-to-projects:hover {
  transition: all 0.2s ease-in-out;
  animation: buttonfade 1s infinite;
  background: rgba(66, 36, 102, 0.721);
}
.go-to-projects:active {
  transform: translate(0px, 3px);
  background: rgba(66, 36, 102, 0.476);
}

.image-container {
  justify-self: center;
  border-radius: 50%;
}
.propic-image {
  background: radial-gradient(
    circle at center,
    rgba(136, 0, 255, 0.1) 0%,
    rgba(136, 0, 255, 0.2) 100%,
    /* Punto di inizio della sfumatura */ transparent
  );
  width: 50vh;
  border-radius: 50%;
  padding: 10px;
}

@keyframes buttonfade {
  0% {
    opacity: 1;
  }
  50% {
    opacity: 0.3;
  }
  100% {
    opacity: 1;
  }
}
